import { Streamlit } from "./streamlit"
import * as L from "leaflet"
import "leaflet/dist/leaflet.css"
// Import the Leaflet.draw CSS and JS files
import "leaflet-draw/dist/leaflet.draw.css"
import "leaflet-draw/dist/leaflet.draw.js"
// import * as EasyButton from 'leaflet-easybutton'
import 'leaflet-easybutton/src/easy-button.css'
import * as EasyButton from 'leaflet-easybutton'


const map = document.createElement("div")
map.style.height = "500px"
map.setAttribute("id", "mapid")
document.body.appendChild(map)
const mymap = L.map("mapid").setView([37.8, -96], 4)
let circles: {center: L.LatLng, radius: number}[] = []


L.tileLayer(
  "https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token={accessToken}",
  {
    attribution:'Â© Access AI',
    maxZoom: 18,
    id: "mapbox/outdoors-v11",
    tileSize: 512,
    zoomOffset: -1,
    accessToken: "pk.eyJ1IjoidGpkMjAyNCIsImEiOiJjbHIyNmU2Z2oweTRmMnFuMWN1dmN1N3V4In0.9dU2pjRURk4qs31aBAV4lg",
  }
).addTo(mymap)


let drawnItems: L.FeatureGroup = new L.FeatureGroup();
mymap.addLayer(drawnItems);

// Create a new L.Control.Draw object and add it to the map
const drawControl = new L.Control.Draw({
  // Specify the options for the drawing tools
  edit: {
    featureGroup: drawnItems
  },
  draw: {
    // Disable the other drawing tools
    polyline: false,
    polygon: false,
    rectangle: false,
    marker: false,
    // Enable the circle tool and customize its style
    circle: {
      metric: false, // Disable metric units
      feet: false, // Disable feet units
      nautic: false, // Disable nautical miles
      // imperial: true, // Enable imperial units (miles)
      shapeOptions: {
        color: "blue",
        weight: 3,
        fill: true,
        fillColor: "lightblue",
        fillOpacity: 0.5
      }
    }
  }
})
mymap.addControl(drawControl)

mymap.on('draw:created', function (e) {
  var layer = e.layer;
  drawnItems.addLayer(layer);  // Add the layer to drawnItems
});

// Add an event listener for the draw:created event
mymap.on("draw:created", function (e: any) {
  // Get the layer that was created by the drawing tool
  const layer = e.layer
  // Add the layer to the map
  mymap.addLayer(layer)
  // Get the center and radius of the circle
  const center = layer.getLatLng()
  const radius = layer.getRadius()
  // // Show a popup with the circle information
  // L.popup()
  //   .setLatLng(center)
  //   .setContent("You drew a circle with center " + center.toString() + " and radius " + radius + " meters")
  //   .openOn(mymap)
  // Add the circle information to the circles array
  circles.push({center, radius})
  // Send the circles array to Streamlit
  Streamlit.setComponentValue(circles)
  Streamlit.setFrameHeight()
})

function onMapClick(e: any) {
  // L.popup()
  //   .setLatLng(e.latlng)
  //   .setContent("You clicked the map at " + e.latlng.toString())
  //   .openOn(mymap)
  Streamlit.setComponentValue(e.latlng)
  Streamlit.setFrameHeight()
}

mymap.on("click", onMapClick)

// Add this new event listener
mymap.on("draw:deleted", function (e: any) {
  // Clear the circles array
  circles = []
  // // Update the Streamlit component value
  // Streamlit.setComponentValue(circles)
  // Streamlit.setFrameHeight()
})

let selectState = false

function onRender(event: Event): void {
  Streamlit.setFrameHeight()
}
Streamlit.events.addEventListener(Streamlit.RENDER_EVENT, onRender)

Streamlit.setComponentReady()
Streamlit.setFrameHeight()
